<template>
    <div class="home-wrapper">
        <v-card
            class="weather-item"
            :class=""
            horizontal
            v-for="forecast, idx in forecastList"
            :key="idx">
            <v-card-row
                class="weather-avatar">
                <icon
                    class="city-icon"
                    :name="'svg-' + forecast.location.city.toLowerCase()"
                    :scale="7"></icon>
                <v-icon
                    class="weather-icon">{{ forecast.item.condition.code | weatherCode2Class | weatherClass2Icon }}</v-icon>
            </v-card-row>
            <v-card-column>
                <v-card-row class="">
                    <v-card-text class="text-xs-right">
                        <div>
                            <v-icon class="place-icon">place</v-icon>
                            <span>{{ forecast.location.city }}</span>
                            <span>{{ forecast.location.country }}</span>
                        </div>
                        <div>
                            <span class="weather-temp">{{ forecast.item.condition.temp | convert2Temp }}&#176;</span>
                        </div>
                    </v-card-text>
                </v-card-row>
                <v-card-row actions class="">
                    <v-btn primary light>
                        <v-icon left light>more_vert</v-icon>More
                    </v-btn>
                </v-card-row>
            </v-card-column>
        </v-card>
    </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex';
import EventBus from '@/event-bus';
import pageLoadingMixin from '@/mixins/pageLoadingMixin';

export default {
    name: 'home',
    mixins: [pageLoadingMixin],
    props: {},
    data() {
        return {};
    },
    computed: {
        ...mapGetters([
            'forecastList'
        ])
    },
    methods: {
        ...mapActions([
            'setPageLoading',
            'showBottomNav',
            'setAppHeader',
            'getForecast'
        ]),
        log() {
            console.log('click action-btn in header');
        }
    },
    async mounted() {
        let cityCodeList = ['2151849', '2151330'];
        await Promise.all(cityCodeList.map(code => {
            return this.getForecast(code);
        }));
        this.setPageLoading(false);
        // 监听app-header事件
        EventBus.$on('app-header:click-action', this.log);
    },
    destroyed() {
        // 销毁时注销
        EventBus.$off('app-header:click-action', this.log);
    },
    activated() {
        this.setAppHeader({
            show: true,
            title: 'VUE-PWA',
            showMenu: true,
            showBack: false,
            showLogo: true,
            actions: [
                {
                    icon: 'search',
                    route: '/search'
                }
            ]
        });
        this.showBottomNav();
        this.setPageLoading(false);
    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    
    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    
        $material-custom := {
            bg-color: #fff
            fg-color: #000
            text-color: #000
            primary-text-percent: 0.87
            secondary-text-percent: 0.54
            disabledORhints-text-percent: 0.38
            divider-percent: 0.12
            active-icon-percent: 0.54
            inactive-icon-percent: 0.38
        }
        $material-theme := $material-custom
    

    .weather-item
        .weather-avatar
            position relative
            flex 1
            margin-top 0
            .city-icon
                color #90a4ae
                position absolute
                left 0
                bottom 0
            .weather-icon
                position absolute
                top 10px
                right 20px
                font-size 50px
        .place-icon
            color #b71c1c
        .weather-temp
            font-size 40px
</style>



